import { Fragment, useEffect, useState } from "react";
import style from "../../optimized/Form/Form.module.css";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import FormInput from "../../optimized/Ui/Input/FormInput";
import FormTextarea from "../../optimized/Ui/Input/FormTextarea";
import CustomSelect from "../../optimized/Ui/Select/CustomSelect";
import FormCalendarInput from "../../optimized/Ui/Input/FormCalendarInput";
import FormFile from "../../optimized/Ui/Input/FormFile";
import CustomCheckbox from "../../optimized/Ui/CustomCheckbox/CustomCheckbox";
import CustomRadioButton from "../../optimized/Ui/CustomRadioButton/CustomRadioButton";
import { useFileUpload } from "../../hooks/authentication-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../ImageCrop/CustomCrop";
import { assestsFeedUrl } from "../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../optimized/Services/UrlConfig/BaseUrl";
import QvipleLoader from "../../optimized/Loader/QvipleLoader";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../optimized/Utils/Functions/dateUtility";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../optimized/Validation/functions/checkingRequiredField";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import Loading from "../../optimized/Loader/Loading";
import ApplyStaffFormPreview from "./ApplyStaffFormPreview";
import { useLocation } from "react-router-dom";
import { useAuthGetAccountWithMobileNumber } from "../../optimized/Hooks/ApiHooks/Auth/auth-api";
import { useSelector } from "react-redux";
import { useStaffApplyFormFieldDetail } from "../../optimized/Hooks/ApiHooks/InstituteStaff/institute-staff-api";
import JoinFormExistingSelect from "../Student/Form/JoinFormExistingSelect";
import CreateButton from "../../optimized/Ui/Button/CreateButton";

const array_field = [
  "staff_qualification_details",
  "staff_research_and_publication",
  "staff_past_experience_details",
];

const insertStaffArrayField = {
  staff_qualification_details: {
    institute_name: "",
    degree_name: "",
    passing_year: "",
  },
  staff_past_experience_details: {
    institute_name: "",
    designation: "",
    from: "",
    to: "",
  },
  staff_research_and_publication: {
    title: "",
    description: "",
    link_url: "",
    attachment: "",
  },
};
const StaffApplyForm = ({ onApplyForm, setPreview, preview }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const ids = useSelector((state) => state.idChange);
  const [stateLabel, setStateLabel] = useState([]);
  const [state, setState] = useState({});
  const [dynamicState, setDynamicState] = useState({});
  const [notRequiredState, setNotRequiredState] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeStep, setActiveStep] = useState("");
  const [fileUpload] = useFileUpload();
  const [sameAs, setSameAs] = useState({
    address: "",
  });
  const [selectedUser, setSelectedUser] = useState("");
  const [alreadyPhone, setAlreadyPhone] = useState("");

  const { authGetAccountWithMobileNumber } = useAuthGetAccountWithMobileNumber({
    phoneKey: alreadyPhone,
    skip: alreadyPhone?.length !== 10,
  });

  const { staffApplyFormFieldDetail } = useStaffApplyFormFieldDetail({
    data: {
      flow: "INSTITUTE",
      flowId:
        getQuery.state?.openAs === "INSTITUTE_SIDE"
          ? ids?.id
          : getQuery.state?.openAs === "DIRECT"
          ? getQuery.state?.instituteId
          : ids.searchProfile?.id,
    },
    skip:
      getQuery.state?.openAs === "INSTITUTE_SIDE"
        ? !ids?.id
        : getQuery.state?.openAs === "DIRECT"
        ? !getQuery.state?.instituteId
        : !ids.searchProfile?.id,
  });

  useEffect(() => {
    if (staffApplyFormFieldDetail) {
      let lb = [];
      let k_lb = {};
      let dy = {};
      let rq = [];
      for (let i = 0; i < staffApplyFormFieldDetail?.length; i++) {
        let st = staffApplyFormFieldDetail[i];

        let obj = {
          section_name: st?.section_name,
          section_key: st?.section_key,
          section_value: st?.section_value,
          field_list: [],
        };
        for (let j = 0; j < st?.form_checklist?.length; j++) {
          let ct = st?.form_checklist[j];
          obj.field_list?.push({
            label: ct?.form_checklist_lable,
            placeholder: ct?.form_checklist_placeholder,
            type: ct?.form_checklist_typo,
            db_key: ct?.form_checklist_key,
            options: ct?.form_checklist_typo_option_pl,
            width: ct?.width,
          });
          if (ct?.form_checklist_key) {
            if (array_field?.includes(ct?.form_checklist_key)) {
              k_lb[ct?.form_checklist_key] = [];
            } else {
              k_lb[ct?.form_checklist_key] = "";
            }
          }
          if (ct?.form_checklist_key_status === "DYNAMIC") {
            dy[ct?.form_checklist_key] = "";
          }
          if (!ct?.form_checklist_required) {
            rq.push(ct?.form_checklist_key);
          }
        }
        lb.push(obj);
      }

      setStateLabel(lb);
      setState(k_lb);
      setDynamicState(dy);
      setNotRequiredState(rq);
    }
  }, [staffApplyFormFieldDetail]);
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [activeStep]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCropFileUpload = (e, flow) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveStep(flow);
    }
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onUploadDocument = (e, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [flow]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCalendarChoose = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(e),
    }));
  };

  const onAddressSame = (e) => {
    setSameAs((prev) => ({
      ...prev,
      address: e.target.checked,
    }));
    setState((prev) => ({
      ...prev,
      staffAddress: prev?.staffCurrentAddress,
      staffPincode: prev?.staffCurrentPincode,
      staffState: prev?.staffCurrentState,
      staffDistrict: prev?.staffCurrentDistrict,
    }));
  };
  const onCheckbox = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e.target.checked,
    }));
  };
  const onGetAllAccount = (e) => {
    setAlreadyPhone(e.target.value?.trim());
  };
  const onSelectedUser = (e) => {
    setSelectedUser(e.target.value);
  };

  const onAddArrayField = (flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: [...prev[flow], { ...insertStaffArrayField[flow] }],
    }));
  };
  const onArrayCalendarChoose = (e, subFlow, ind, flow) => {
    let inp = [...state[flow]];
    inp[ind][subFlow] = ymdSlashDateFormat(e);
    setState((prev) => ({
      ...prev,
      [flow]: inp,
    }));
  };
  const onArrayUploadDocument = (e, subFlow, ind, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          let inp = [...state[flow]];
          inp[ind][subFlow] = res.data?.imageKey;
          setState((prev) => ({
            ...prev,
            [flow]: inp,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onArrayChange = (e, ind, flow) => {
    let inp = [...state[flow]];
    inp[ind][e.target.name] = e.target.value;
    setState((prev) => ({
      ...prev,
      [flow]: inp,
    }));
  };
  const onPreview = (e) => {
    e.preventDefault();
    let validation = null;
    if (getQuery.state?.openAs === "INSTITUTE_SIDE") {
      validation = requiredField(
        { ...state, mobileNumber: alreadyPhone },
        notRequiredState
      );
    } else {
      validation = requiredField(state, notRequiredState);
    }
    if (existingRequiredField(validation)) {
      setPreview(true);
    } else {
      setErrorField(validation);
    }
  };

  // console.info("getQuery", errorField);

  return (
    <>
      {preview ? (
        <ApplyStaffFormPreview
          state={state}
          stateLabel={stateLabel}
          onApplyStudentForm={onApplyForm}
          dynamicState={dynamicState}
          otherState={{
            alreadyPhone: alreadyPhone,
            selectedUser: selectedUser,
            allAccount: authGetAccountWithMobileNumber ?? [],
          }}
        />
      ) : (
        <form className={style.fc_container} onSubmit={onPreview}>
          {getQuery.state?.openAs === "INSTITUTE_SIDE" ? (
            <div className={style.fccs_inner}>
              <FormInput
                label={t("form_mobile_no_label")}
                placeholder={t("form_student_mobile_number_placeholder")}
                name={"mobileNumber"}
                value={alreadyPhone}
                type={"text"}
                onChange={onGetAllAccount}
                customFormInputContainer={{
                  width: "49%",
                }}
                errorShow={errorField["mobileNumber"]}
              />
              {authGetAccountWithMobileNumber?.length ? (
                <JoinFormExistingSelect
                  selectLabel={t("existing_account")}
                  selectedValue={t("select_existing_account")}
                  value={selectedUser}
                  name={"user"}
                  onChange={onSelectedUser}
                  options={authGetAccountWithMobileNumber}
                  customStyleContainer={{
                    width: "49%",
                  }}
                  customInputStyle={{
                    height: "42px",
                  }}
                  customLabelStyle={{
                    marginBlock: "0.7rem",
                  }}
                />
              ) : (
                ""
              )}
            </div>
          ) : null}

          {stateLabel?.length > 0 ? null : <Loading />}
          {stateLabel?.map((sl, index) =>
            index === 0 ? (
              <section
                className={style.fcc_section}
                key={`${sl?.section_name}${index}`}
              >
                <div
                  className={style.fccs_inner}
                  style={{
                    justifyContent: "flex-end",
                    marginTop: "1rem",
                  }}
                >
                  {sl?.field_list?.map((fd, d_index) => (
                    <div className={style.fcci_container}>
                      <label
                        htmlFor={`${fd?.db_key}${d_index}`}
                        className={style.fcc_label}
                      >
                        <img
                          className={style.fcc_photo}
                          src={
                            state[fd?.db_key]
                              ? `${imageShowUrl}/${state[fd?.db_key]}`
                              : `${assestsFeedUrl}/feed-user-avatar.svg`
                          }
                          alt="student avatar"
                        />
                        <img
                          src="/images/user-pencil-edit-transparent.svg"
                          alt="edit icon"
                          className={style.fcce_icon}
                        />
                      </label>
                      <input
                        type="file"
                        id={`${fd?.db_key}${d_index}`}
                        onChange={(e) => onCropFileUpload(e, fd?.db_key)}
                        style={{ display: "none" }}
                      />
                      <p>
                        {fd?.label ?? ""}
                        {errorField[fd?.db_key] ? (
                          <span
                            style={{
                              color: "rgb(231, 75, 75)",
                            }}
                          >
                            * {t("required")}
                          </span>
                        ) : null}
                      </p>
                    </div>
                  ))}
                </div>
              </section>
            ) : (
              <section
                className={style.fcc_section}
                key={`${sl?.section_name}${index}`}
              >
                <h4>{sl?.section_name}:</h4>
                <div className={style.fccs_inner}>
                  {sl?.field_list?.map((fl, f_index) =>
                    fl?.type === "TEXT" ? (
                      <FormInput
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        placeholder={fl?.placeholder}
                        name={fl?.db_key}
                        type={fl?.type}
                        value={state[fl?.db_key]}
                        onChange={onChange}
                        errorShow={errorField[fl?.db_key]}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      />
                    ) : fl?.type === "TEXTAREA" ? (
                      <FormTextarea
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        placeholder={fl?.placeholder}
                        name={fl?.db_key}
                        type={fl?.type}
                        value={state[fl?.db_key]}
                        onChange={onChange}
                        errorShow={errorField[fl?.db_key]}
                        customFormInputContainer={{
                          width: "100%",
                        }}
                      />
                    ) : fl?.type === "NUMBER" ? (
                      <FormInput
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        placeholder={fl?.placeholder}
                        name={fl?.db_key}
                        type={fl?.type}
                        value={state[fl?.db_key]}
                        onChange={onChange}
                        errorShow={errorField[fl?.db_key]}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      />
                    ) : fl?.type === "SELECT" ? (
                      <CustomSelect
                        key={`${fl?.db_key}${f_index}`}
                        selectLabel={fl?.label}
                        selectedValue={fl?.placeholder}
                        defalutValue={state[fl?.db_key]}
                        onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                        options={fl?.options || []}
                        errorShow={errorField[fl?.db_key]}
                        customStyleContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                        customListStyle={{
                          position: "absolute",
                          width: "100%",
                          zIndex: "1",
                        }}
                      />
                    ) : fl?.type === "MULITSELECT" ? (
                      <CustomSelect
                        key={`${fl?.db_key}${f_index}`}
                        selectLabel={fl?.label}
                        selectedValue={fl?.placeholder}
                        defalutValue={state[fl?.db_key]}
                        onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                        options={fl?.options || []}
                        errorShow={errorField[fl?.db_key]}
                        customStyleContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                        customListStyle={{
                          position: "absolute",
                          width: "100%",
                          zIndex: "1",
                        }}
                      />
                    ) : fl?.type === "RADIO" ? (
                      <CustomRadioButton
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        options={["Java", "C++", "C", "C#"]}
                        value={state[fl?.db_key]}
                        onChange={onChange}
                        errorShow={errorField[fl?.db_key]}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      />
                    ) : fl?.type === "CHECKBOX" ? (
                      <CustomCheckbox
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        options={fl?.options}
                        defaultValue={state[fl?.db_key]}
                        onChange={(e) => onCheckbox(e, fl?.db_key)}
                        errorShow={errorField[fl?.db_key]}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                        isCheck
                      />
                    ) : fl?.type === "CALENDAR" ? (
                      <FormCalendarInput
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        placeholder={fl?.placeholder}
                        name={fl?.db_key}
                        type={fl?.type}
                        value={
                          state[fl?.db_key]
                            ? slashDateFormat(state[fl?.db_key])
                            : ""
                        }
                        onChange={(e) => onCalendarChoose(e, fl?.db_key)}
                        errorShow={errorField[fl?.db_key]}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      />
                    ) : fl?.type === "FILE" ? (
                      <FormFile
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        placeholder={fl?.placeholder}
                        name={fl?.db_key}
                        type={fl?.type}
                        value={
                          state[fl?.db_key]
                            ? {
                                key: state[fl?.db_key],
                              }
                            : ""
                        }
                        onChange={(e) => onUploadDocument(e, fl?.db_key)}
                        errorShow={errorField[fl?.db_key]}
                        customFormFileContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      />
                    ) : fl?.type === "CROPIMAGE" ? (
                      <Fragment key={`${fl?.db_key}${f_index}`}>
                        {fl?.form_checklist_key ===
                        "studentProfilePhoto" ? null : (
                          <FormFile
                            label={fl?.label}
                            placeholder={fl?.placeholder}
                            name={fl?.db_key}
                            type={fl?.type}
                            value={
                              state[fl?.db_key]
                                ? {
                                    key: state[fl?.db_key],
                                  }
                                : ""
                            }
                            onChange={onChange}
                            errorShow={errorField[fl?.db_key]}
                            customFormFileContainer={{
                              width: fl?.width ? `${fl?.width}` : "49%",
                            }}
                          />
                        )}
                      </Fragment>
                    ) : fl?.type === "Same As" ? (
                      <CustomCheckbox
                        key={`${fl?.db_key}${f_index}234`}
                        // label={fl?.label || "Same As Current Address"}
                        // options={fl?.options}
                        options={["Same As Current Address"]}
                        defaultValue={sameAs.address}
                        onChange={onAddressSame}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                        isCheck
                      />
                    ) : fl?.type === "BUTTON" ? (
                      <Fragment key={`${fl?.db_key}${f_index}`}>
                        {fl?.db_key === "staff_qualification_details" &&
                          state[fl?.db_key]?.map((st, index) => (
                            <div
                              key={`${state[fl?.db_key]}${index}ty`}
                              className={style.fccs_inner}
                            >
                              <FormInput
                                label="Institute Name"
                                placeholder="Enter Institute name"
                                name="institute_name"
                                type="text"
                                value={st?.institute_name}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormInput
                                label="Degree Name"
                                placeholder="Enter Degree name"
                                name="degree_name"
                                type="text"
                                value={st?.degree_name}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormInput
                                label="Passing Year"
                                placeholder="Enter Passing year"
                                name="passing_year"
                                type="text"
                                value={st?.passing_year}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                            </div>
                          ))}
                        {fl?.db_key === "staff_past_experience_details" &&
                          state[fl?.db_key]?.map((st, index) => (
                            <div
                              key={`${state[fl?.db_key]}${index}ty`}
                              className={style.fccs_inner}
                            >
                              <FormInput
                                label="Institute Name"
                                placeholder="Enter Institute name"
                                name="institute_name"
                                type="text"
                                value={st?.institute_name}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormInput
                                label="Designation"
                                placeholder="Enter Designation"
                                name="designation"
                                type="text"
                                value={st?.designation}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormCalendarInput
                                label="From"
                                placeholder="Select Date"
                                name="from"
                                value={
                                  st?.from ? slashDateFormat(st?.from) : ""
                                }
                                onChange={(e) =>
                                  onArrayCalendarChoose(
                                    e,
                                    "from",
                                    index,
                                    fl?.db_key
                                  )
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormCalendarInput
                                label="To"
                                placeholder="Select Date"
                                name="to"
                                value={st?.to ? slashDateFormat(st?.to) : ""}
                                onChange={(e) =>
                                  onArrayCalendarChoose(
                                    e,
                                    "to",
                                    index,
                                    fl?.db_key
                                  )
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                            </div>
                          ))}
                        {fl?.db_key === "staff_research_and_publication" &&
                          state[fl?.db_key]?.map((st, index) => (
                            <div
                              key={`${state[fl?.db_key]}${index}ty`}
                              className={style.fccs_inner}
                            >
                              <FormInput
                                label="Title"
                                placeholder="Enter title"
                                name="title"
                                type="text"
                                value={st?.title}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormInput
                                label="Link"
                                placeholder="Enter link"
                                name="link_url"
                                type="text"
                                value={st?.link_url}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "49%",
                                }}
                              />
                              <FormTextarea
                                label="Description"
                                placeholder="Enter description"
                                name="description"
                                type="text"
                                value={st?.description}
                                onChange={(e) =>
                                  onArrayChange(e, index, fl?.db_key)
                                }
                                customFormInputContainer={{
                                  width: "100%",
                                }}
                              />
                              <FormFile
                                label="Attachment"
                                name="attachment"
                                type="file"
                                value={
                                  st?.attachment
                                    ? {
                                        key: st?.attachment,
                                      }
                                    : ""
                                }
                                onChange={(e) =>
                                  onArrayUploadDocument(
                                    e,
                                    "attachment",
                                    index,
                                    fl?.db_key
                                  )
                                }
                                customFormFileContainer={{
                                  width: "49%",
                                }}
                              />
                            </div>
                          ))}
                        <CreateButton
                          label="add"
                          customStyle={{
                            paddingBlock: "0.2rem",
                            margin: "0",
                            marginTop: "0.4rem",
                          }}
                          onAction={() => onAddArrayField(fl?.db_key)}
                        />
                      </Fragment>
                    ) : null
                  )}
                </div>
                <BorderBottom />
              </section>
            )
          )}
          <FormCreateButton
            label="preview"
            customStyle={{
              width: "100%",
            }}
          />
        </form>
      )}

      {openCrop && (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

