import {
  useInstituteStaffAddSectionMutation,
  useInstituteStaffFormDetailQuery,
  useInstituteStaffRearrangeSectionMutation,
  useInstituteStaffSectionAddChecklistMutation,
  useInstituteStaffUpdateSectionChecklistMutation,
  useInstituteStaffToggleChecklistMutation,
  useStaffProfileAboutDetailQuery,
  useStaffApplyFormFieldDetailQuery,
} from "../../../Services/InstituteStaff/instituteStaffApiDynamic";

export const useInstituteStaffAddSection = () => {
  const [instituteStaffAddSection] = useInstituteStaffAddSectionMutation();
  return [instituteStaffAddSection];
};
export const useInstituteStaffSectionAddChecklist = () => {
  const [instituteStaffSectionAddChecklist] =
    useInstituteStaffSectionAddChecklistMutation();
  return [instituteStaffSectionAddChecklist];
};
export const useInstituteStaffRearrangeSection = () => {
  const [instituteStaffRearrangeSection] =
    useInstituteStaffRearrangeSectionMutation();
  return [instituteStaffRearrangeSection];
};
export const useInstituteStaffUpdateSectionChecklist = () => {
  const [instituteStaffUpdateSectionChecklist] =
    useInstituteStaffUpdateSectionChecklistMutation();
  return [instituteStaffUpdateSectionChecklist];
};

export const useInstituteStaffFormDetail = (args) => {
  const { data, refetch, isFetching } = useInstituteStaffFormDetailQuery(
    args?.data,
    {
      skip: args?.skip,
    }
  );
  return {
    instituteStaffFormDetail: data,
    instituteStaffFormDetailRefetch: refetch,
    instituteStaffFormDetailLoading: isFetching,
  };
};
export const useInstituteStaffToggleChecklist = () => {
  const [instituteStaffToggleChecklist] =
    useInstituteStaffToggleChecklistMutation();
  return [instituteStaffToggleChecklist];
};

