import { PATCH, POST } from "../UrlConfig/MethodType";

export const instituteStaffAddSectionFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/ins/new/form/${data.formId}/section/staff/query`,
      method: POST,
      body: data.bodyParameter,
    }),
  });
};

