// for showing field in staff side

export const staffApplyFormFieldDetailFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/ins/dynamic/form/details/query?flow=${data?.flow}&did=${data?.flowId}`,
    transformResponse: (response) =>
      response?.ins_form ? response?.ins_form : response?.depart_form,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

//for staff profile detail

export const staffProfileAboutDetailFunction = (builder) => {
  return builder.query({
    query: (data) => `/v1/ins/${data.sid}/dynamic/form/query`,
    // keepUnusedDataFor: 0,
    transformResponse: (response) => response?.result,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
