export const {
  useInstituteStaffAddSectionMutation,
  useInstituteStaffSectionAddChecklistMutation,
  useInstituteStaffRearrangeSectionMutation,
  useInstituteStaffUpdateSectionChecklistMutation,
  useInstituteStaffFormDetailQuery,
  useInstituteStaffToggleChecklistMutation,
  useStaffProfileAboutDetailQuery,
  useStaffApplyFormFieldDetailQuery,
} = instituteStaffApiDynamic;
