import { useTranslation } from "react-i18next";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import style from "../../optimized/UserStudentMember/StudentProfile/StudentProfile.module.css";
import { Fragment, useCallback } from "react";
import { imageShowUrl } from "../../services/BaseUrl";
import { assestsFeedUrl } from "../../optimized/Services/UrlConfig/AssestsBaseUrl";
import styleForm from "../../optimized/Form/Form.module.css";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import CustomCheckbox from "../../optimized/Ui/CustomCheckbox/CustomCheckbox";

function ApplyStaffFormPreview({
  onApplyStudentForm,
  stateLabel,
  state,
  dynamicState,
  otherState,
}) {
  const { t } = useTranslation();

  const onViewFile = useCallback((iKey) => {
    if (iKey) {
      const newWindow = window.open(
        `${imageShowUrl}/${iKey}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  }, []);
  const onFormSubmit = (e) => {
    e.preventDefault();
    onApplyStudentForm({
      state: state,
      dynamicState: dynamicState,
      otherState: otherState,
    });
  };
  return (
    <>
      <form onSubmit={onFormSubmit}>
        <section className={style.sdpa}>
          {otherState?.alreadyPhone ? (
            <div className={style.sdpa_section}>
              <div className={style.sdpac_wrap}>
                <div
                  className={style.sdpas_container}
                  style={{
                    width: "49%",
                  }}
                >
                  <p className={style.sdpasc_label}>
                    {t("form_mobile_no_label")} :
                  </p>
                  <h6 className={style.sdpasc_value}>
                    {otherState?.alreadyPhone}
                  </h6>
                </div>
                {otherState?.allAccount?.map((usr) =>
                  usr?._id === otherState?.selectedUser ? (
                    <div
                      className={style.sdpas_container}
                      style={{
                        width: "49%",
                      }}
                    >
                      <p className={style.sdpasc_label}>
                        {t("existing_account")} :
                      </p>
                      <h6
                        className={style.sdpasc_value}
                      >{`${usr?.userLegalName} - (${usr?.username})`}</h6>
                    </div>
                  ) : (
                    ""
                  )
                )}
              </div>
              <BorderBottom />
            </div>
          ) : null}

          {stateLabel?.map((sl, index) =>
            index === 0 ? (
              <section
                className={styleForm.fcc_section}
                key={`${sl?.section_name}${index}`}
              >
                <div
                  className={styleForm.fccs_inner}
                  style={{
                    justifyContent: "flex-end",
                    marginTop: "1rem",
                  }}
                >
                  {sl?.field_list?.map((fd, d_index) => (
                    <div className={styleForm.fcci_container}>
                      <label
                        htmlFor={`${fd?.db_key}${d_index}`}
                        className={styleForm.fcc_label}
                      >
                        <img
                          className={styleForm.fcc_photo}
                          src={
                            state[fd?.db_key]
                              ? `${imageShowUrl}/${state[fd?.db_key]}`
                              : `${assestsFeedUrl}/feed-user-avatar.svg`
                          }
                          alt="student avatar"
                        />
                      </label>
                      <p>{fd?.label ?? ""}</p>
                    </div>
                  ))}
                </div>
              </section>
            ) : (
              <div
                className={style.sdpa_section}
                key={`${sl?.section_name}${index}`}
              >
                <h5>{sl?.section_name}:</h5>
                <div className={style.sdpac_wrap}>
                  {sl?.field_list?.map((fl, f_index) =>
                    fl?.type === "FILE" ? (
                      <div
                        className={style.sdpas_container}
                        key={`${fl?.db_key}${f_index}`}
                        style={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      >
                        <p className={style.sdpasc_label}>{fl?.label} :</p>
                        {state[fl?.db_key] ? (
                          <h6
                            className={style.sdpasc_value}
                            onClick={() => onViewFile(state[fl?.db_key])}
                            style={{
                              cursor: "pointer",
                              color: "#002BBE",
                            }}
                          >
                            {t("view_file")}
                          </h6>
                        ) : null}
                      </div>
                    ) : fl?.type === "CHECKBOX" ? (
                      <CustomCheckbox
                        key={`${fl?.db_key}${f_index}`}
                        label={fl?.label}
                        options={fl?.options}
                        defaultValue={state[fl?.db_key]}
                        onChange={() => {}}
                        customFormInputContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                        customFormInputLabel={{
                          height: "0",
                          margin: "0",
                        }}
                        isCheck
                      />
                    ) : fl?.type === "BUTTON" ? (
                      <Fragment key={`${fl?.db_key}${f_index}`}>
                        {fl?.db_key === "staff_qualification_details" &&
                          state[fl?.db_key]?.map((st, index) => (
                            <div
                              key={`${state[fl?.db_key]}${index}ty`}
                              className={styleForm.fccs_inner}
                            >
                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Institute Name :
                                </p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.institute_name ?? ""}
                                </h6>
                              </div>

                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Degree Name :
                                </p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.degree_name ?? ""}
                                </h6>
                              </div>

                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Passing Year :
                                </p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.passing_year ?? ""}
                                </h6>
                              </div>
                            </div>
                          ))}
                        {fl?.db_key === "staff_past_experience_details" &&
                          state[fl?.db_key]?.map((st, index) => (
                            <div
                              key={`${state[fl?.db_key]}${index}ty`}
                              className={styleForm.fccs_inner}
                            >
                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Institute Name :
                                </p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.institute_name ?? ""}
                                </h6>
                              </div>

                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Designation :
                                </p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.designation ?? ""}
                                </h6>
                              </div>

                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>From :</p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.from ?? ""}
                                </h6>
                              </div>
                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>To :</p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.to ?? ""}
                                </h6>
                              </div>
                            </div>
                          ))}
                        {fl?.db_key === "staff_research_and_publication" &&
                          state[fl?.db_key]?.map((st, index) => (
                            <div
                              key={`${state[fl?.db_key]}${index}ty`}
                              className={styleForm.fccs_inner}
                            >
                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>Title :</p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.title ?? ""}
                                </h6>
                              </div>

                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>Link :</p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.link_url ?? ""}
                                </h6>
                              </div>

                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "100%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Description:
                                </p>
                                <h6 className={style.sdpasc_value}>
                                  {st?.description ?? ""}
                                </h6>
                              </div>
                              <div
                                className={style.sdpas_container}
                                key={`${fl?.db_key}${f_index}`}
                                style={{
                                  width: fl?.width ? `${fl?.width}` : "49%",
                                }}
                              >
                                <p className={style.sdpasc_label}>
                                  Attachment :
                                </p>
                                {st?.attachment ? (
                                  <h6
                                    className={style.sdpasc_value}
                                    onClick={() => onViewFile(st?.attachment)}
                                    style={{
                                      cursor: "pointer",
                                      color: "#002BBE",
                                    }}
                                  >
                                    {t("view_file")}
                                  </h6>
                                ) : null}
                              </div>
                            </div>
                          ))}
                      </Fragment>
                    ) : (
                      <div
                        className={style.sdpas_container}
                        key={`${fl?.db_key}${f_index}`}
                        style={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      >
                        <p className={style.sdpasc_label}>{fl?.label} :</p>
                        <h6 className={style.sdpasc_value}>
                          {state[fl?.db_key]}
                        </h6>
                      </div>
                    )
                  )}
                </div>
                <BorderBottom />
              </div>
            )
          )}
        </section>
        <FormCreateButton
          label="submit"
          customStyle={{
            width: "97%",
            marginInline: "1rem",
          }}
        />
      </form>
    </>
  );
}

export default ApplyStaffFormPreview;
